.components{
    position: fixed;
    width: 100%;
    display: flex;
    
}

.left-component{
    width: 34vw;
    background-color :#842bd7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    
}

.left-content{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.head-text{
    color:#fff;
    font-size: 3.375vw;
}
.head-text-2{
    color:#fff;
    font-size: 2.0vw;
}

.btn-1{
    width: 14.688rem;
    height: 3.375rem;
    background-color: #842bd7;
    color:#fff;
    border-radius: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 30px;
    
}

.right-component{
    width: 66vw;
    background-color:#fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.head{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    margin-bottom: 35px;
    cursor: pointer;
    
}


.body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.link{
    color:#fff;
    font-weight: bold;
    font-size: 3vw;
}


form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.head-text-3{
    color:#000;
    font-size: 3.375vw;
}

.head-text-4{
    color: #555a59;
    font-size: 1.5vw;
}


.btn-2{
    
    width: 14.688rem;
    height: 3.375rem;
    background-color: #fff;
    color:#000;
    border-radius: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 30px;
    
}

.btn-1:hover {
    opacity: 0.7;
}
.btn-2:hover {
    opacity: 0.7;
}

.input{
    width: 29rem;
    height: 3rem;
    background: #eef5f3;
    border-radius: 20px;
    border: none;
    outline: none;
    margin: 0.8rem;
    padding: 0 16px;
    
}

.fa {
    padding: 20px;
    font-size: 30px;
    width: 30px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 50%;
  }

  
  
.fa:hover {
      opacity: 0.7;
  }
  
  .fa-facebook {
    background: #3B5998;
    color: white;
  }
 
  .fa-google {
    background: #dd4b39;
    color: white;
  }


@media all and (max-width:800px){
    .components{
        position: fixed;
        display: flex;
        flex-direction: column;
    }
    .left-component{
        height: 10vh;
        width: 100vw;
        
    }
    .right-component{
        height: 90vh;
        width: 100vw;
       
        
    }

    .input{
        width: 25rem;
    }

   
    .btn-1{
        margin-bottom: 20px;
    }

    .head{
        display: none;
    }

    .head-text-2{
        font-size: 3vw;
    }

    .head-text-3{
        font-size: 6vw;
    }

    .head-text-4{
        font-size: 3vw;
    }

}

@media all and (max-width:420px){
    .input{
        width: 17em;
    }

    .head-text-2 {
        font-size: 4vw;
    }


    .head-text-3{
        font-size: 7vw;
    }

    .head-text-4{
        font-size: 4vw;
    }

}

@media all and (max-width:320px){
    .input{
        width: 12em;
        height: 2rem;
    }

    .head-text-3{
        display: none;
    }

    .head-text-4{
        margin-top: 20px;
        font-size: 5vw;
    }
}

