.components{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    
}

.left-component{
    width: 66vw;
    background-color:#fff;
}

.head{
    position: relative;
    top: 25px;
    left: 25px;
    cursor: pointer;
    z-index: 30;
}


.body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.link{
    color:#fff;
    font-weight: bold;
    font-size: 3vw;
}

form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.head-text-3{
    color:#000;
    font-size: 3.375rem;
}

.head-text-4{
    color: #555a59;
    font-size: 1.5rem;
}

.btn-2{
    width: 14.688rem;
    height: 3.375rem;
    background-color: #fff;
    color:#000;
    border-radius: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 30px;
    
}

.btn-2:hover {
    opacity: 0.7;
}

.input{
    width: 29rem;
    height: 3rem;
    font-weight: bold;
    background: #eef5f3;
    border-radius: 20px;
    border: none;
    outline: none;
    margin: 0.8rem;
    padding: 0 16px;
    
}

.fa {
    padding: 20px;
    font-size: 30px;
    width: 30px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 50%;
  }

  
  
.fa:hover {
      opacity: 0.7;
  }
  
  .fa-facebook {
    background: #3B5998;
    color: white;
  }
 
  .fa-google {
    background: #dd4b39;
    color: white;
  }



.right-component{
    width: 34vw;
    background-color: #842bd7;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.right-content{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}


.head-text{
    color:#fff;
    font-size: 3.375rem;
}
.head-text-2{
    color:#fff;
    font-size: 2.0rem;
}

.btn-1{
    width: 14.688rem;
    height: 3.375rem;
    background-color: #842bd7;
    color:#fff;
    border-radius: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 30px;
    
}


.btn-1:hover {
    opacity: 0.7;
}

.forgot-pass{
    margin-top: 1rem;
}